	module.exports = {"data":{"markdownRemark":{"html":"<p>例大祭に参加してきました。<a href=\"/!/thC/\">東方夢終劇 ～ Concealed the Conclusion</a>もほどほどの数が皆様のお手元に渡っていきました、誠にありがとうございました。そしてド修羅場の中仕上げてもらった関係者の方々には頭が上がりません。 しかし正直な話プレ体験版と言うところもありあまり多数配れなかったので、 (あと頒布当日の説明が不十分で<a href=\"/!/thA/\">時封城</a>を知らない方へのとっつきが悪かったとか反省点を挙げてしまうとキリがないですが)後日何らかの手段で入手し損ねた方のためにも頒布できたらなと思います。Web頒布出来るならそれが理想ですがわざわざ体験版入手された方に怒られてしまいそうですしなんかいい方法ないかなぁと悩み中。とりあえず今週いっぱいは半休み状態で来週から改めて本格的に製作に取っ組む予定。 今回は例大祭報告・挨拶だけの更新です。そして以下拍手レス。</p>\n<h2>(拍)例大祭で夢終劇買いましたが動きません。</h2>\n<p><a href=\"https://twitter.com/danmaq\">掲示板</a>の方にどのように動かないのかをもう少し詳しく書いていただけないでしょうか？</p>\n<h2>(拍)Web拍手の返事しましょうよ</h2>\n<p>もしかして毎日リアルタイムに返事すれってことですか？＿？</p>","frontmatter":{"cover":null,"date":"2006-05-23T19:12:00+09:00","strDate":"2006/5/23","title":"例大祭乙でした"}}},"pathContext":{"path":"/ja/blog/2006/05/23-例大祭乙でした/","langKey":"ja"}}

